"use client";

import { useEffect, useState, useCallback } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Calendar, Clock, ExternalLink, AlertCircle } from "lucide-react";

interface CalInstance {
  (
    action: string,
    options?: {
      origin?: string;
      elementOrSelector?: string;
      calLink?: string;
      layout?: string;
      config?: {
        layout?: string;
        theme?: string;
        locale?: string;
      };
    }
  ): void;
  ns?: {
    [key: string]: (action: string, options?: Record<string, unknown>) => void;
  };
}

declare global {
  interface Window {
    Cal?: CalInstance;
  }
}

export function BookingCalendar() {
  const [isClient, setIsClient] = useState(false);
  const [calLoaded, setCalLoaded] = useState(false);
  const [calError, setCalError] = useState(false);
  const [retryCount, setRetryCount] = useState(0);

  useEffect(() => {
    setIsClient(true);
  }, []);

  const initializeCal = useCallback(() => {
    try {
      if (window.Cal) {
        window.Cal("init", {
          origin: "https://cal.com"
        });
        
        // Wait a bit for init to complete, then initialize inline embed
        setTimeout(() => {
          if (window.Cal) {
            window.Cal("inline", {
              elementOrSelector: "#my-cal-inline",
              calLink: "afarsemon/40min",
              layout: "month_view",
              config: {
                layout: "month_view",
                theme: "auto",
                locale: "he"
              }
            });

            // UI configuration for inline embed
            setTimeout(() => {
              try {
                window.Cal.ns?.["afarsemon40min"]?.("ui", {
                  theme: "auto",
                  styles: {
                    branding: {
                      brandColor: "#000000"
                    }
                  },
                  hideEventTypeDetails: false,
                  layout: "month_view"
                });
              } catch (uiError) {
                console.warn("Cal.com UI configuration failed:", uiError);
                // This is not critical, continue without UI config
              }
            }, 100);

            setCalLoaded(true);
            setCalError(false);
          }
        }, 200);
      } else {
        throw new Error("Cal object not available");
      }
    } catch (error) {
      console.error("Error initializing Cal.com:", error);
      setCalError(true);
      
      // Retry up to 3 times with increasing delays
      if (retryCount < 3) {
        setTimeout(() => {
          setRetryCount(prev => prev + 1);
          initializeCal();
        }, 1000 * (retryCount + 1));
      }
    }
  }, [retryCount]);

  useEffect(() => {
    if (!isClient) return;

    // Check if script already exists
    let script = document.querySelector('script[src="https://app.cal.com/embed/embed.js"]') as HTMLScriptElement;
    
    if (script) {
      // Script already loaded, try to initialize
      if (window.Cal) {
        initializeCal();
      } else {
        script.onload = () => initializeCal();
      }
    } else {
      // Load Cal.com embed script
      script = document.createElement("script");
      script.src = "https://app.cal.com/embed/embed.js";
      script.async = true;
      document.body.appendChild(script);

      script.onload = () => {
        // Give the script time to initialize the Cal object
        setTimeout(() => {
          initializeCal();
        }, 100);
      };

      script.onerror = () => {
        console.error("Failed to load Cal.com embed script");
        setCalError(true);
      };
    }

    return () => {
      // Don't remove the script on cleanup to avoid reload issues
      // The script can be reused across component mounts
    };
  }, [isClient, initializeCal]);

  const openInNewTab = () => {
    window.open("https://cal.com/afarsemon/40min", "_blank", "noopener,noreferrer");
  };

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle className="text-2xl">קבעו פגישה</CardTitle>
            <CardDescription className="flex items-center gap-2 mt-2">
              <Calendar className="h-4 w-4" />
              <span>בחרו זמן נוח לשיחת ייעוץ של 40 דקות</span>
              <Clock className="h-4 w-4 mr-2" />
            </CardDescription>
          </div>
          <Button 
            variant="outline" 
            size="sm"
            onClick={openInNewTab}
            className="flex items-center gap-2"
          >
            פתח בחלון חדש
            <ExternalLink className="h-4 w-4" />
          </Button>
        </div>
      </CardHeader>
      <CardContent>
        {isClient ? (
          <div 
            id="my-cal-inline" 
            style={{ 
              width: "100%", 
              height: "650px",
              overflow: "auto",
              borderRadius: "8px"
            }}
          >
            <div className="flex items-center justify-center h-full text-muted-foreground">
              <div className="text-center">
                <Calendar className="h-12 w-12 mx-auto mb-4 animate-pulse" />
                <p>טוען את לוח הזמנים...</p>
                <Button 
                  variant="link" 
                  onClick={openInNewTab}
                  className="mt-2"
                >
                  או לחצו כאן לפתיחה בחלון חדש
                </Button>
              </div>
            </div>
          </div>
        ) : (
          <div 
            style={{ 
              width: "100%", 
              height: "650px",
              borderRadius: "8px"
            }}
            className="flex items-center justify-center bg-muted/20"
          >
            <div className="text-center text-muted-foreground">
              <Calendar className="h-12 w-12 mx-auto mb-4" />
              <p>הכנת לוח הזמנים...</p>
            </div>
          </div>
        )}
        
        <div className="mt-4 p-4 bg-muted/50 rounded-lg">
          <p className="text-sm text-muted-foreground">
            <strong>פגישת ייעוץ אישית:</strong> 40 דקות של ייעוץ מקצועי בנושא אוטומציות AI לעסק שלכם.
            נדבר על הצרכים שלכם ונבנה תוכנית פעולה מותאמת אישית.
          </p>
        </div>
      </CardContent>
    </Card>
  );
}